import './index.css'
import $ from 'jquery'
import echarts from 'echarts'
import { fetchInterval } from '@/util/request'
import tableHbs from './templates/table.hbs'
import barOpt from './options/barOpt'
import lineOpt from './options/lineOpt'
import { deformationToOrigin } from '@/util/zoom/zoom'

export default class BorderFlow {
  /**
   * 请求表格数据
   * @param  {String}  type selector 的 type
   * @return {Promise} Promise
   */
  fetchFlowTop5 = type => {
    console.log(type)
    return fetchInterval('fetchFlowTop5', { type }, data => {
      $('#borderFlowTable table')
        .empty()
        .append(tableHbs(data.list))
      // 浮窗
      $('#borderFlowTable .mouse-hover')
      .on('mousemove', ev => {
        let Ev = ev || window.event
        let pos = deformationToOrigin([Ev.pageX , Ev.pageY])
        $('.com-floating .value').html($(Ev.currentTarget).attr('data-title'))
        $('.com-floating').stop(true, true).fadeIn(500)
        $('.com-floating').css({'transform': `translate(${pos[0] + 30}px, ${pos[1] + 30}px)`})
      }).on('mouseout', () => {
        $('.com-floating .value').empty()
        $('.com-floating').stop(true, true).fadeOut(500)
      })
    }, 600000)
  }

  render() {
    // 渲染柱形图
    fetchInterval('fetchFlowStatus', data => {
      const chart = echarts.init($('#borderFlowBarChart')[0])
      chart.setOption(barOpt(data.list.map(d => d.value), data.list.map(d => d.name)))
    }, 2000)

    // 渲染表格
    this.fetchFlowTop5('police')
    $('#borderFlowTable select').on('change', e => {
      this.fetchFlowTop5(e.target.value)
    })

    // 渲染折线图
    fetchInterval('fetchFlowTrend', data => {
      const chart = echarts.init($('#borderFlowLineChart')[0])
      chart.setOption(lineOpt(data))
    }, 600000)

  }
}



