import './index.css'
import $ from 'jquery'
import { take } from 'lodash'
import hbs from './index.hbs'
import tableHbs from './table.hbs'

export default class Modal {
  constructor(headers = []) {
    this.limit = 8
    this.page = 1
    this.totalPage = 0

    this.headers = headers

    this.dataSource = null
  }

  /**
   * 计算分页起始和结束下标
   * @return {Array} [start, end]
   */
  countIndex = () => {
    const start = this.page * this.limit - this.limit
    const end = this.page * this.limit - 1

    return [start, end]
  }

  rebuildTable = () => {
    const list = []
    const range = this.countIndex()

    for (let i = 0; i < this.dataSource.length; i ++) {
      if (i >= range[0] && i <= range[1]) {
        list.push(this.dataSource[i])
      }
    }

    // 清除当前的列表数据
    $('#modalTable')
      .empty()
      .append(tableHbs({
        headers: this.headers,
        dataSource: list
      }))
  }

  bindPrevClick = () => {
    $('#modalPrev').on('click', () => {
      if (this.page > 1) {
        this.page = this.page - 1

        this.rebuildTable()
      }
    })
  }

  bindNextClick = () => {
    $('#modalNext').on('click', () => {
      if (this.page < this.totalPage) {
        this.page = this.page + 1

        this.rebuildTable()
      }
    })
  }

  render(dataSource) {
    this.dataSource = dataSource
    const data = take(dataSource, this.limit)
    console.log(data.length)
    
    $('main').prepend(hbs())
    $('#modalTable').append(tableHbs({
      headers: this.headers,
      dataSource: data
    }))
    // 没数据时添加暂无数据
    if(data.length === 0) {
      $('#modalTable').append('<span class="com-hint">暂无数据</span>')
    }
    // 计算当前数据的总页数
    this.totalPage = Math.ceil(dataSource.length / this.limit)

    this.bindNextClick()
    this.bindPrevClick()
  }
}



