export default {
  'url': '/networkSecurity/featureLibrary',
  'mock': {
    'code': 1,
    'msg': 'success',
    'result': {
      'count|1000-8000': 1000,
      'updatetime': '@datetime',
      'updateCount|10': 1,
      'features|5': [
        {
          'id': '@id',
          'name': '@features',
          'status': 1,
          'list|5': [
            {
              'company': '@company',
              'username': '@cname',
              'IP': '@ip',
              'type': '@features',
              'level': '@dangerlevel',
              'source': '来源边界'
            }
          ]
        }
      ],
      'notFeatures|50': [
        {
          'id': '@id',
          'name': '@features',
          'status': 0
        }
      ]
    }
  }
}



