import $ from 'jquery'
import './style.css'
// 引入模板
import sideNavHbs from './sideNav.hbs'

export default class leftPage {

  /**
   *  事件绑定
   */
  bindEvent() {
    $('.side-nav-btn').on('click', function(){
      let judge = $('.side-nav-btn').hasClass('active')
      if(judge) {
        $('.side-l-nav, .side-nav-btn').removeClass('active')
      } else {
        $('.side-l-nav, .side-nav-btn').addClass('active')
      }
    })
  }

  /**
   *  初始化
   */
  init(){
    let href = location.href.split('/')
    let ip = href[2]
    $('.side-l-nav').html(sideNavHbs({
      data: [
        {
          name: '首页',
          href: 'http://' + ip + '/jizhen-aqts/totalTrend.html',
          active: false
        }, {
          name: '终端安全监测',
          href: 'http://' + ip + '/jizhen-aqts/terminalSecurity.html',
          active: false
        }, {
          name: '网络安全监测',
          href: 'http://' + ip + '/jizhen-aqts/networkSecurity.html',
          active: false
        }, {
          name: '数据安全监测',
          href: 'http://' + ip + '/jizhen-aqts/dataSecurity.html',
          active: false
        }, {
          name: '终端安全态势',
          href: 'http://' + ip + '/jizhen-aqts/terminalTrend.html',
          active: true
        }
      ]
    }))
    this.bindEvent()
  }
}




