import Mock from 'mockjs'

Mock.Random.extend({
  service: function() {
    const d = ['大数据服务', '基础服务', '病毒安全监测服务', '边界流量监测服务', '网络安全监测服务', '病毒查杀服务', '大数据挖掘服务']
    return this.pick(d)
  },
  serviceType: function() {
    const d = ['文件', '接口', '数据文档']
    return this.pick(d)
  },
  direction: function() {
    const d = ['内到外', '外到内']
    return this.pick(d)
  }
})

export default {
  'url': '/networkSecurity/flowTop5/:type',
  'mock': {
    'code': 1,
    'msg': 'success',
    'result': {
      'list|3': [
        {
          'name': '@service',
          'type': '@serviceType',
          'total|1-5': 1,
          'time': '@datetime'
        }
      ]
    }
  }
}



