import $ from 'jquery'
import theme from './theme'
import slotHbs from './slot.hbs'
import { randomString } from '../util/util'

export default class Slot {
  /**
   * 配置默认属性
   * @param  {Object} opt 配置项
   * @return {void}   void
   */
  defaultConfig(opt) {
    this.duration = opt.duration || 1500
    this.digit = opt.digit || 5
    this.styles = Object.assign(theme, opt.styles)
  }

  /**
   * 初始化样式表
   * @return {void}   void
   */
  initStyle() {
    this.containerclass = `container${randomString(10)}`
    this.groupclass = `group${randomString(10)}`
    this.itemclass = `item${randomString(10)}`

    $('head').append(`
      <style>
        .${this.containerclass} {
          display: flex;
          height: ${this.styles.fontSize}px;
          overflow: hidden;
        }
        .${this.groupclass} {
          transition: all ${this.duration}ms ease 0ms;
          transform: translateY(0px);
          margin-left: ${this.styles.padding}px;
        }
        .${this.groupclass}:nth-child(1) {
          margin-left: 0;
        }
        .${this.itemclass} {
          font-family: ${this.styles.fontFamily};
          font-size: ${this.styles.fontSize}px;
          color: ${this.styles.color};
        }
      </style>
    `)
  }

  /**
   * 初始化节点
   * 该方法会根据 digit 的数量来渲染对应数量的节点
   * @param  {String} selector 选择器
   * @return {void}   void
   */
  initDom(selector) {
    let classes = {
      container: this.containerclass,
      chunk: []
    }
    // 循环 numArr 数组，并为每个节点添加 class
    for (let i = 0; i < this.digit; i ++) {
      classes.chunk.push({
        group: this.groupclass,
        item: this.itemclass
      })
    }
    $(selector).html(slotHbs(classes))
  }

  constructor(selector, opt = {}) {
    this.defaultConfig(opt)
    this.initStyle()
    this.initDom(selector)
  }

  /**
   * 变换节点
   * @param  {Array} dataset 数据
   * @return {void}  void
   */
  transDom(dataset) {
    // 为每个节点增加 transform 属性
    // ==========================
    for (let [i, d] of dataset.entries()) {
      $(`.${this.groupclass}`).eq(i)
        .css({
          display: d === 'none' ? 'none' : '',
          transform: `translateY(-${d * this.styles.fontSize}px)`
        })
    }
  }

  /**
   * 渲染
   * @param  {String} number 必填，数据
   * @return {void}   void
   */
  render(number) {
    // 筛选过滤
    // =======
    if (!number) {
      throw new Error('not defined param number!')
    }
    if (!this.digit) {
      throw new Error('not defined param digit!')
    }
    if (Number(this.digit) - String(number).length < 0) {
      throw new Error('digit must greater than or equal to number\'s length!')
    }
    if (!Number(number)) {
      throw new Error('number must Number!')
    }

    // 将传过来的数据重新组装成数组
    // 如果 digit 位数大于实际数字的位数
    // 则给数组添加 0 作为占位符
    // ======================
    let numArr = String(number).split('')
    let diff = Number(this.digit) - numArr.length
    if (diff > 0) {
      for (let i = 0; i < diff; i ++) {
        numArr.unshift('none')
      }
    }

    this.transDom(numArr)
  }
}



