import './index.css'
import $ from 'jquery'
import echarts from 'echarts'
import { fetch, fetchInterval } from '@/util/request'
import { ModalServer } from '@/components'
import hbs from './index.hbs'
import barOpt from './barOpt'

export default class ServerSecurity {
  constructor() {
    this.currentPage = 0
  }

  /**
   * @param  {Object} btn     DOM对象
   * @param  {Number} type    类型
   * @param  {Array}  headers 表头数组
   * @return {void}   void
   */
  bindClickEvent = (btn, type, headers) => {
    const url = 'fetchServerSecurityDetail'
    const limit = 8

    this.currentPage = 0

    btn.off('click')
    btn.on('click', () => {
      fetch(url, { type, limit, currentPage: this.currentPage }, dataSource => {
        const modal = new ModalServer({
          headers,
          // 点击上一页
          onPrev: () => {
            if (this.currentPage > 0) {
              this.currentPage -= 1
              console.log(this.currentPage)
              fetch(url, { type, limit, currentPage: this.currentPage }, data => {
                modal.renderTable(data.list)
              })
            } else {
              alert('这是第一页!')
            }
          },
          // 点击下一页
          onNext: () => {
            this.currentPage += 1
            console.log(this.currentPage)
            fetch(url, { type, limit, currentPage: this.currentPage }, data => {
              if (data.list.length > 0) {
                modal.renderTable(data.list)
              } else {
                this.currentPage -= 1
                alert('这是最后一页!')
              }
            })
          }
        })

        modal.init(dataSource.list)
      })
    })
  }

  render() {
    fetchInterval('fetchServerSecurity', data => {
      $('#serverSecurity')
        .empty()
        .append(hbs(data))

      // 渲染柱形图
      const chart = echarts.init($('#serverSecurityChart')[0])
      chart.setOption(barOpt(data.dangerTop5, data.dangerTop5.map(d => d.name)))

      // 绑定值的弹框事件
      this.bindClickEvent(
        $('#serverSecurity .btn-virus'), 0,
        ['姓名', '单位', 'IP', '病毒名称', '病毒类型', '处置状态'])
      this.bindClickEvent(
        $('#serverSecurity .btn-defeat'), 1,
        ['姓名', '单位', 'IP', '病毒名称', '病毒类型'])
      this.bindClickEvent(
        $('#serverSecurity .btn-attack'), 2,
        ['姓名', '单位', 'IP', '攻击名称', '攻击类型', '危险等级', '处置状态'])
      this.bindClickEvent(
        $('#serverSecurity .btn-dispose'), 3,
        ['姓名', '单位', 'IP', '病毒名称', '病毒类型'])
    }, 600000)
  }
}



