export default {
  'url': '/networkSecurity/serverSecurity/getDetail/:type/:limit/:currentPage',
  'mock': {
    'code': 1,
    'msg': 'success',
    'result': {
      'list|8': [
        {
          'name': '@cname',
          'company': '@company',
          'IP': '@ip',
          'virusName': '@virusNames',
          'virusType': '@features',
          'resolve': '@resolve',

          'attackName': '攻击名称',
          'attackType': '攻击类型',
          'dangerLevel': '@dangerlevel'
        }
      ]
    }
  }
}



