
import './index.css'
import $ from 'jquery'
import loader from '@/loader/loader'
import apis from './mock/index'
import { fetch } from '@/util/request'
import LoadNav from '@/components/loadNav/index'

let loadNav = new LoadNav()
import {
  Header,
  InternetMonitor,
  ServerSecurity,
  BorderFlow
} from './components'

const header = new Header()
const internetMonitor = new InternetMonitor()
const serverSecurity = new ServerSecurity()
const borderFlow = new BorderFlow()

loader.load({
  apis,
  init() {
    loadNav.init() //  页面导航
    $('.side-l-nav li:eq(2)').addClass('active')
    // 判断是否有用户登录
    fetch('fetchLoginUsername', data => {
      if(data.name != undefined) {
        header.render(data)
        internetMonitor.render()
        serverSecurity.render()
        borderFlow.render()
      } else {
        alert('请先登录')
        fetch('fetchLogOut', data => {
            $(window).attr('location', data.url)
        })
      }
      this.addEvent()
    })
  },
  // 绑定事件
  addEvent() {
    // 点击退出
    $('.user-box').on('click', '.log-out', () => {
      fetch('fetchLogOut', data => {
          $(window).attr('location', data.url)
      })
    })
  }
}, 3360, 1890)



