import './index.css'
import $ from 'jquery'
import { take } from 'lodash'
import hbs from './index.hbs'
import tableHbs from './table.hbs'

export default class Modal {
  constructor({headers = [], onPrev, onNext}) {
    this.headers = headers
    this.onPrev = onPrev
    this.onNext = onNext

    this.dataSource = null
  }

  /**
   * 绑定弹窗关闭事件
   * @return {void} void
   */
  bindCloseBtnClick = () => {
    $('#btnModalClose').on('click', function() {
      $(this).off('click')
      $('#modalContainer').remove()
      $('#modalReturn').stop().hide()
    })
  }

  /**
   * 渲染表格
   * @param  {Array} dataSource 数据集
   * @return {void}  void
   */
  renderTable = dataSource => {
    // 这里删除了一个固定属性，原因是后端说他们那边没法处理，然后就我们处理了一下。
    dataSource.map(d => {
      delete d['ROW_ID']
    })
    $('#modalTable')
      .empty()
      .append(tableHbs({
        headers: this.headers,
        dataSource
      }))
    // 没数据时添加暂无数据
    if(dataSource.length === 0) {
      $('.modal-wrapper').append('<span class="com-hint">暂无数据</span>')
    }
  }

  /**
   * 渲染
   * @param  {Array} dataSource 数据集
   * @return {void}  void
   */
  init = dataSource => {

    // 渲染基础组件, 并绑定事件
    $('main').prepend(hbs())
    this.bindCloseBtnClick()
    $('#modalPrev').on('click', this.onPrev)
    $('#modalNext').on('click', this.onNext)

    this.renderTable(take(dataSource, 8))
  }
}



