import echarts from 'echarts'

const bar = (name, data, colors) => ({
  name,
  type: 'bar',
  data,
  barWidth: 20,
  barMinHeight: 10,
  barGap: '100%',
  itemStyle: {
    color: new echarts.graphic.LinearGradient(
      0, 0, 0, 1,
      [
        { offset: 0, color: colors[0] },
        { offset: 0.5, color: colors[1] },
        { offset: 1, color: colors[2] }
      ]
    ),
    barBorderRadius: [20, 20, 0, 0]
  }
})

export default (data, domain) => ({
  title: {
    show: false
  },
  tooltip: {
    trigger: 'axis',
    textStyle: {
      fontSize: 26
    }
  },
  legend: {
    data: [
      '病毒数量',
      '查杀数',
      '攻击数',
      '处置数'
    ],
    right: '6%',
    inactiveColor: '#4f4f4f',
    itemGap: 40,
    textStyle: {
      fontSize: 26,
      color: '#FFF'
    }
  },
  grid: {
    left: '3%',
    right: '4%',
    bottom: '3%',
    containLabel: true
  },
  xAxis: {
    type: 'category',
    axisLine: {
      lineStyle: {
        color: '#424e77'
      }
    },
    axisTick: {
      show: false
    },
    axisLabel: {
      color: '#a4d5ff',
      fontSize: 24
    },
    data: domain
  },
  yAxis: {
    type: 'value',
    axisLine: {
      show: false
    },
    axisTick: {
      show: false
    },
    axisLabel: {
      fontSize: 26,
      color: '#35e6fc'
    },
    splitLine: {
      lineStyle: {
        width: 1,
        color: '#424e77'
      }
    }
  },
  series: [
    bar('病毒数量', data.map(d => d.value[0]),
      ['rgba(53, 231, 253, 1)', 'rgba(53, 231, 253, .5)', 'rgba(53, 231, 253, .1)']),
    bar('查杀数', data.map(d => d.value[1]),
      ['rgba(231, 189, 7, 1)', 'rgba(231, 189, 7, .5)', 'rgba(231, 189, 7, .1)']),
    bar('攻击数', data.map(d => d.value[2]),
      ['rgba(19, 74, 231, 1)', 'rgba(113, 65, 205, .5)', 'rgba(113, 65, 205, .1)']),
    bar('处置数', data.map(d => d.value[3]),
      ['rgba(21, 230, 96, 1)', 'rgba(21, 230, 96, .5)', 'rgba(21, 230, 96, .1)'])
  ]
})



