import './index.css'
import $ from 'jquery'
import { fetchInterval } from '@/util/request'
import statisticsHbs from './templates/statistics.hbs'
import featuresHbs from './templates/features.hbs'
import { uniqWith, shuffle, isEqual, flattenDeep} from 'lodash'
import { deformationToOrigin } from '@/util/zoom/zoom'

export default class Internet {
  constructor() {
    this.t = null
    this.init = false
  }

  render() {
    // 宏观统计
    fetchInterval('fetchNetworkStatistics', data => {
      $('#networkStatistics')
        .empty()
        .append(statisticsHbs({ class: 'flow', name: '网络流量(G)', value: data.flow }))
        .append(statisticsHbs({ class: 'attack', name: '攻击事件数', value: data.attack }))
        .append(statisticsHbs({ class: 'intercept', name: '拦截数', value: data.intercept }))
    }, 2000)

    // 特征库
    fetchInterval('fetchFeatureLibrary', data => {
      // 模拟扫描
      // 测试数据
      // data.features = [{
      //   'id': '@id',
      //   'name': '@features',
      //   'status': 1,
      //   'list': [
      //     {
      //       'company': '@company',
      //       'username': '@cname',
      //       'IP': '@ip',
      //       'type': '@features',
      //       'level': '@dangerlevel',
      //       'source': '来源边界'
      //     }
      //   ]
      // }]
      data.newData = this.newFeatures(data)
      $('#feature')
        .empty()
        .append(featuresHbs(data))
      if(data.newData.newList.length === 0){
        $('#featureTable').append('<span class="com-hint">暂无数据</span>')
      }
      clearInterval(this.scan)
      this.scan = setInterval(() => {
        data.newData = this.newFeatures(data)
        $('#feature')
          .empty()
          .append(featuresHbs(data))
        this.addEvent()
        if(data.newData.newList.length === 0){
          $('#featureTable').append('<span class="com-hint">暂无数据</span>')
        }
      }, 10000)
      
      // 列表轮播
      if (!this.init) {
        this.init = true
        clearInterval(this.t)
        this.t = setInterval(() => {
          $('#featureTable')
            .find('ul')
            .first()
            .stop()
            .animate({ marginTop: '-80px' }, () => {
              $('#featureTable')
                .find('ul')
                .first()
                .css({ marginTop: '0px' })
                .appendTo('#featureTable')
            })
        }, 4000)
      }
      
      this.addEvent()
    }, 300000)
  }

  addEvent() {
    // 浮窗
    $('.feature-group > div').on('mousemove', ev => {
      let Ev = ev || window.event
      let pos = deformationToOrigin([Ev.pageX , Ev.pageY])
      $('.com-floating .value').html($(Ev.currentTarget).attr('data-title'))
      $('.com-floating').stop(true, true).fadeIn(500)
      $('.com-floating').css({'transform': `translate(${pos[0] + 30}px, ${pos[1] + 30}px)`})
    }).on('mouseout', () => {
      $('.com-floating .value').empty()
      $('.com-floating').stop(true, true).fadeOut(500)
    })
  }

  /**
   *  重新更新网络安全
   *  @param    {object}  data 原数组
   *  @return   {object}  更新后的数组
   */
  newFeatures(data) {
    let newFeatures = []
    let newList = []
    let sjNum = data.features.length / 2
    if(sjNum > 5) {
      sjNum = Math.ceil(Math.random() * 4) 
    }
    if(data.features.length === 1) {
      sjNum = 1
    }
    sjNum = Math.floor(sjNum)
    
    // 随机选中数个数
    let activeRandom = Math.round(Math.random() * sjNum) 
    // 选中池中的数量
    let activeLength = data.features.length
    let activeLength1 = data.notFeatures.length
    // 循环10组数据
    for(let i = 0; i <= 10; i++){
    // while (newFeatures.length < 6) {
      if(newFeatures.length < activeRandom){
        let num1 = Math.ceil(Math.random() * (activeLength - 1)) 
        newFeatures.push(data.features[num1])
      } else {
        let num2 = Math.ceil(Math.random() * (activeLength1 - 1))
        newFeatures.push(data.notFeatures[num2])
      }
      // 去重
      newFeatures = shuffle(uniqWith(newFeatures, isEqual()))
      i = newFeatures.length
    // }
    }
    newFeatures.forEach(e => {
      if(e.list != undefined) {
        newList.push(e.list)
      }
      e.acronymName = e.name.substring(0,10) + '...'
    })
    newList = flattenDeep(newList)
    // let newLists = newList
    return {
      'newFeatures': newFeatures,
      'newList': newList
    }
  }
}



